"use client";

import { useState, useEffect } from "react";

export default function Hero() {
  const [text, setText] = useState("");
  const fullText = "Full Stack Developer";
  const [isDeleting, setIsDeleting] = useState(false);
  const [speed, setSpeed] = useState(150);

  useEffect(() => {
    const timer = setTimeout(() => {
      if (!isDeleting && text.length < fullText.length) {
        setText(fullText.substring(0, text.length + 1));
        setSpeed(150);
      } else if (isDeleting && text.length > 0) {
        setText(fullText.substring(0, text.length - 1));
        setSpeed(100);
      } else if (!isDeleting && text.length === fullText.length) {
        setTimeout(() => setIsDeleting(true), 2000);
      } else if (isDeleting && text.length === 0) {
        setIsDeleting(false);
      }
    }, speed);

    return () => clearTimeout(timer);
  }, [text, isDeleting, speed, fullText]);

  return (
    <section id="hero" className="min-h-screen flex items-center justify-center relative overflow-hidden">
      <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900 to-black" />
      
      <div className="relative z-10 text-center px-4">
        <h1 className="text-5xl md:text-7xl font-bold mb-4">
          Hello, I'm{" "}
          <span className="bg-gradient-to-r from-white to-gray-400 bg-clip-text text-transparent">
            Your Name
          </span>
        </h1>
        
        <div className="text-2xl md:text-3xl mb-8 text-gray-300">
          <span className="border-r-2 border-white animate-pulse">
            {text}
          </span>
        </div>
        
        <p className="text-lg md:text-xl text-gray-400 max-w-2xl mx-auto mb-8">
          I create exceptional digital experiences through clean code and innovative solutions
        </p>
        
        <div className="flex gap-4 justify-center">
          <a
            href="#projects"
            className="px-8 py-3 bg-white text-black font-semibold rounded-full hover:bg-gray-200 transition-colors"
          >
            View My Work
          </a>
          <a
            href="#contact"
            className="px-8 py-3 border border-white text-white font-semibold rounded-full hover:bg-white hover:text-black transition-colors"
          >
            Get In Touch
          </a>
        </div>
      </div>
      
      <div className="absolute bottom-10 left-1/2 transform -translate-x-1/2 animate-bounce">
        <div className="w-6 h-10 border-2 border-white rounded-full flex justify-center">
          <div className="w-1 h-3 bg-white rounded-full mt-2 animate-pulse" />
        </div>
      </div>
    </section>
  );
}
